import React from 'react';
import PropTypes from 'prop-types';

function Wrapper(props) {
    const Course =
        props.initialResults &&
        props.initialResults.results &&
        props.initialResults.results.map((e, i) => {
            let code = e.listMetadata.code && e.listMetadata.code[0],
                title = e.title;
            return (
                <li key={i}>
                    {title}
                    {code}
                </li>
            );
        });
    return (
        <form method="get">
            <h2>Search for a course</h2>
            {Course}
        </form>
    );
}

Wrapper.propTypes = {
    config: PropTypes.object,
    initialResults: PropTypes.object,
    element: PropTypes.object,
};

export default Wrapper;
